@font-face {
  font-family: "Arial";
  src: url("../assets/fonts/ArialMT.woff2") format("woff2"), url("../assets/fonts/ArialMT.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  line-height: 2.4rem;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0em;
}

h1 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 3.3rem;
  line-height: 3.52rem;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0em;
  margin: 10px 0px;
}

textarea {
  width: 100%;
  height: 200px;
  resize: vertical;
}

p {
  margin: 0px;
}

span {
  user-select: none;
  transition: all 0.3s;
}

.hidden {
  display: none;
}

.container {
  max-width: 720px;
  margin: auto;
  scroll-behavior: smooth;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.keyboard_box {
  width: 100%;
}

.keyboard {
  width: 100%;
  padding: 10px 10px 0px 0px;
  border: 2px solid #000000;
  border-radius: 10px;
}

.keyboard__row {
  display: flex;
  width: 100%;
  margin-bottom: 10px;
}

.row__key {
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: 10px;
  border: 2px solid #000000;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
}
.row__key:hover span {
  font-weight: bold;
  transition: all 0.3s;
}

.eng, .rus {
  display: flex;
  justify-content: center;
}

.Space {
  flex-shrink: 0.58;
}

.ShiftLeft, .ShiftRight {
  flex-shrink: 0.93;
}

.Enter {
  flex-shrink: 0.9;
}

.Tab {
  flex-shrink: 0.94;
}

.active {
  background-color: green;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}